import { Component, Injector, Input, OnInit, Type } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { CoreException } from '@nequi/commons/exceptions';
import { CashInOptions } from '@nequi/features/cash-in/core';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { MapsProvider, PointsType } from '@nequi/features/commons/providers';
import {
  InvalidDocumentException,
  NoLinkedAccountsException,
  NoPassWhiteListException,
  ProcessType,
  SendInteractor
} from '@nequi/features/send/core';
import { MfpResponseException } from '@nequi/mfp/exceptions';
import {
  CardModalService,
  FirstTimeMessageOrganism,
  InformativeContentOrganism,
  LoadingService,
  NavRootBottomSheetBaseComponent,
  ToastService
} from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { openUrl, stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { CashInConfig } from '../cash-in.config';
import { OtherBankInfoComponent } from '../other-bank-info/other-bank-info.component';

@Component({
  selector: 'app-cash-in-options',
  templateUrl: './cash-in-options.organism.html',
  styleUrls: ['./cash-in-options.organism.scss'],
})
export class CashInOptionsOrganism extends NavRootBottomSheetBaseComponent implements OnInit {
  @Input() public idOptions: Array<CashInOptions>;
  public options = [];
  public showHelp: boolean;

  constructor(
    private loadingService: LoadingService,
    private sendInteractor: SendInteractor,
    private translateProvider: TranslateProvider,
    private toastService: ToastService,
    private cardModalService: CardModalService,
    private mapsProvider: MapsProvider,
    private navigatorProvider: NavigatorProvider,
    private modalController: ModalController,
    private analyticsInteractor: AnalyticsInteractor,
    private translateProvder: TranslateProvider,
    injector: Injector
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.options = this.getOptions();
    this.validateShouldShowHelp();
  }

  public onOptionClicked(option) {
    switch (option.id) {
      case CashInOptions.BANCOLOMBIA:
        this.setAnalyticsIntention(option.title);

        return this.cashinBank();
      case CashInOptions.BANISTMO:
      case CashInOptions.CASH:
        this.setAnalyticsIntention(option.title);

        return this.navigateToOrganism(
          InformativeContentOrganism,
          option.info,
          this.openNequiPoints.bind(this)
        );
      case CashInOptions.ACH:
        this.setAnalyticsIntention(option.title);

        return this.navigateToOrganism(
          InformativeContentOrganism,
          option.info,
          this.openOtherBankModal.bind(this)
        );
      case CashInOptions.PSE:
        this.setAnalyticsIntention(option.title);

        // TODO: Fix url pse
        return this.navigateToOrganism(
          FirstTimeMessageOrganism,
          option.info,
          this.openPSE.bind(this)
        );
      default:
        return this.bottomSheetController.dismiss(option.url);
    }
  }

  public async openOtherBankModal() {
    this.bottomSheetController.setCanResizeBottomSheetComponent(false);
    const modal = await this.modalController.create({
      component: OtherBankInfoComponent,
    });

    await modal.present();

    this.bottomSheetController.setCanResizeBottomSheetComponent(true);
  }

  public close() {
    this.bottomSheetController.dismiss(null);
  }

  public goToHelp() {
    this.close();
    this.navigatorProvider.navigateForward(CashInConfig.help, {
      queryParams: {
        context: CashInConfig.context,
      },
    });
  }

  private validateShouldShowHelp() {
    this.showHelp = this.idOptions.includes(CashInOptions.HELP);
  }

  private async cashinBank() {
    try {
      await this.loadingService.show();
      const account = await this.sendInteractor.validate(ProcessType.LINK_ACCOUNTS);

      account.filter((bankAccount) => bankAccount.isLinked);

      await this.loadingService.hideWithoutFeedback();

      return this.bottomSheetController.dismiss(CashInConfig.routes.bankRecharge);
    } catch (error) {
      if (error instanceof NoLinkedAccountsException) {
        await this.loadingService.hideWithoutFeedback();
        this.linkBankAccountBoottomSheet();

        return;
      }

      if (error instanceof MfpResponseException) {
        await this.loadingService.hide(false);
        this.toastService.showError(error.message);

        return;
      }

      this.validateCashinBankModal(error);
    }
  }

  private async linkBankAccountBoottomSheet() {
    const bottomSheetMessages = this.translateProvider.get(
      'backpack.home.bottom-sheet.link-bank-account'
    );

    this.forward(FirstTimeMessageOrganism, {
      icon: bottomSheetMessages.image,
      message: bottomSheetMessages.message,
      content: bottomSheetMessages.content,
      url: bottomSheetMessages.url,
      cssClass: 'first-time-message--icon-standard',
      header: {
        view: true,
      },
      button: {
        label: bottomSheetMessages.button,
        color: 'guayaba',
        fill: 'outline',
        action: async () => {
          this.bottomSheetController.dismiss(CashInConfig.routes.linkBankAccount);
        },
      },
    });
  }

  private async validateCashinBankModal(error: CoreException) {
    let i18nKey = 'backpack.home.modals.trapped-genie.no-banck-accounts';
    let acceptAction: () => void;

    if (error instanceof InvalidDocumentException) {
      i18nKey = 'backpack.home.modals.trapped-genie.invalid-document';
    }

    if (error instanceof NoPassWhiteListException) {
      acceptAction = () =>
        this.navigateToOrganism(
          FirstTimeMessageOrganism,
          CashInConfig.pseInfo,
          this.openPSE.bind(this)
        );

      i18nKey = 'backpack.home.modals.trapped-genie.no-pass-white-list';
    }

    const modal = this.translateProvider.get(i18nKey);

    await this.loadingService.hide(false);
    await this.cardModalService.show({
      title: modal.header,
      imgPath: modal.image,
      content: modal.content,
      buttons: [
        {
          text: modal.buttons.accept,
          handler: acceptAction,
        },
        {
          text: modal.buttons.cancel.text,
          isLink: modal.buttons.cancel.isLink,
          handler: async () => {
            if (modal.buttons.cancel.link) {
              openUrl(modal.buttons.cancel.link);
            }
          },
        },
      ],
    });
  }

  private async navigateToOrganism(
    component: Type<unknown>,
    info: string,
    action: () => void,
    cssClass?: string
  ) {
    const messages = this.translateProvider.get(info);

    this.forward(component, {
      ...messages,
      header: { view: true },
      cssClass: cssClass ?? 'first-time-message--icon-standard',
      button: {
        label: messages.button,
        color: 'guayaba',
        fill: 'outline',
        action: action,
      },
    });
  }

  private openPSE() {
    openUrl(CashInConfig.urlPse);
    this.bottomSheetController.dismiss(null);
  }

  private async openNequiPoints() {
    // ToDo: Retrieve radius from Dynamo Parameter
    await this.mapsProvider.launch({
      pointsType: PointsType.RECHARGE,
      radius: CashInConfig.radiusMap,
    });

    this.bottomSheetController.dismiss(null);
  }

  private getOptions() {
    if (this.idOptions) {
      return CashInConfig.options.filter(
        (option: any) =>
          this.idOptions.findIndex((el) => el !== CashInOptions.HELP && el === option.id) !== -1
      );
    }
  }

  private setAnalyticsIntention(optionSelected) {
    const eventProperty = CashInConfig.analytics.events.intentionOfCashIn;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: eventProperty.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: eventProperty.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventProperty.name,
        eventProps: stringsArrayToObjectWithValues(eventProperty.params.cleverTap, [
          getFormattedDateTimes(),
          this.translateProvder.get(optionSelected),
        ]),
      },
    });
  }
}
