import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
  UrlTree
} from '@angular/router';
import { CashInConfigProvider } from '@nequi/features/cash-in/config';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { BottomSheetController, NavRootBottomSheetComponent } from '@nequi/ui';
import { isTypeOf } from '@nequi/utils/types';

import { CashInOptionsOrganism } from '../organisms/cash-in-options.organism';

@Injectable()
export class HomeMiddleware implements CanActivate {
  constructor(
    private bottomSheetController: BottomSheetController,
    private router: Router,
    private translateProvider: TranslateProvider,
    private moduleConfigProvider: ModuleConfigProvider,
    private cashInConfigProvider: CashInConfigProvider
  ) {}

  public async canActivate(
    _route: ActivatedRouteSnapshot,
    _state: RouterStateSnapshot
  ): Promise<UrlTree> {
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);

    const route = await this.showBottomSheet();

    if (isTypeOf<string>(route, String)) {
      return this.router.createUrlTree([route]);
    }
  }

  private async showBottomSheet(): Promise<string> {
    const bottomSheet = this.bottomSheetController.create({
      component: NavRootBottomSheetComponent,
      componentProps: {
        inputProps: {
          idOptions: this.cashInConfigProvider.cashInIdOptions,
        },
        rootPage: CashInOptionsOrganism,
      },
    });

    await bottomSheet.open();

    return (await bottomSheet.onDidDismiss()).data;
  }
}
