import { NgModule, Type } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { CashInConfig } from '../config';
import { HomeMiddleware } from './middleware/home.middleware';

const routes: Routes = [
  {
    path: '',
    component: Type,
    canActivate: [HomeMiddleware],
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(I18nModuleResolver.setup(routes, [CashInConfig.i18n.moduleKey])),
  ],
  exports: [RouterModule],
})
export class FeaturesCashInRoutingModule {}
