import { Component, Injector, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { NavRootBottomSheetBaseComponent } from '@nequi/ui';

import { OtherBankInfoConfig } from './other-bank-info.config';

export interface ItemDetail {
  image: string;
  title: string;
  content: string;
}

@Component({
  selector: 'app-other-bank-info',
  templateUrl: './other-bank-info.component.html',
  styleUrls: ['./other-bank-info.component.scss'],
})
export class OtherBankInfoComponent extends NavRootBottomSheetBaseComponent implements OnInit {
  public infoMessages: Array<ItemDetail>;
  public commision: number;

  constructor(private modalController: ModalController, injector: Injector) {
    super(injector);
  }

  public ngOnInit() {
    this.infoMessages = OtherBankInfoConfig.i18n.infoMessages;
  }

  public close() {
    this.modalController.dismiss();
  }
}
