import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { IonicModule } from '@ionic/angular';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { NequiUiModule } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { CashInConfigProvider } from '../config';
import { CashInConfig } from '../config/cash-in.config';
import { FeaturesCashInRoutingModule } from './features-cash-in-routing.module';
import { HomeMiddleware } from './middleware/home.middleware';
import { CashInOptionsOrganism } from './organisms/cash-in-options.organism';
import { OtherBankInfoComponent } from './other-bank-info/other-bank-info.component';

@NgModule({
  imports: [CommonModule, FeaturesCashInRoutingModule, NequiUiModule, TranslateModule, IonicModule],
  declarations: [CashInOptionsOrganism, OtherBankInfoComponent],
  providers: [
    HomeMiddleware,
    {
      provide: ModuleConfigProvider,
      useValue: CashInConfig,
    },
  ],
})
export class FeaturesCashInModule {
  public static forChild(config: CashInConfigProvider): ModuleWithProviders<FeaturesCashInModule> {
    return {
      ngModule: FeaturesCashInModule,
      providers: [
        {
          provide: CashInConfigProvider,
          useValue: config,
        },
      ],
    };
  }
}
